export default class {
    constructor(max){
        this.datas = [];
        this.max = max;
    }

    push(data){
        if(this.datas.length == this.max){
            this.datas.shift();
        }
        this.datas.push(data);
    }

    calc(period){
        if(period > this.max){
            throw 'out of move average size';
        }
        let size = period > this.datas.size ? this.datas.length : period;
        let sum = 0;
        for (let i = this.datas.length - size; i < this.datas.length; i++) {
            sum += this.datas[i];
        }
        return sum/size;
    }
}